import { Component, OnInit } from '@angular/core';
import { Blog } from '../_model/blog';
import { BlogService } from '../_service/blog.service';
import { Router } from '@angular/router';
import { IconParser } from '../_model/iconParser';

@Component({
  selector: 'app-blog',
  templateUrl: './blog.component.html',
  styleUrls: ['./blog.component.less']
})
export class BlogComponent implements OnInit {

  constructor(
    private blogService: BlogService,
    private router: Router
  ) { }

  private blogs: Blog[];
  private selectedBlogName: string;
  private thisUrl: string[];
  private iconParser: IconParser;

  ngOnInit() {
    this.blogService.blogToDisplay.subscribe(b => {
      this.selectedBlogName = b.name;
    });
    this.iconParser = this.blogService.getIconParser();
    this.thisUrl = this.blogService.buildPath(window.location.pathname);
    this.loadBlogs(this.thisUrl);
  }

  loadBlogs(path: string[]) {
    this.blogService.getBlogList(path).subscribe(bs => {
      this.blogs = [...bs];
    });
  }

  loadIcon(blog: Blog): string {
    if (this.iconParser) {
      return this.blogService.getBlogIconUrl(this.iconParser, blog);
    }
  }

  loadOpenFolderIcon(): string {
    if (this.iconParser) {
      return this.blogService.getOpenFolderIconUrl(this.iconParser);
    }
  }

  select(blog: Blog) {
    if (this.blogService.isDir(blog)) {
      this.thisUrl.push(blog.name);
      this.router.navigate([...this.thisUrl]);
      this.loadBlogs(this.thisUrl);
    } else {
      this.render([...this.thisUrl, blog.name]);
    }
  }

  back(): void {
    this.thisUrl.pop();
    this.router.navigate(this.thisUrl);
    this.loadBlogs(this.thisUrl);
  }
  render(path: string[]) {
    this.blogService.getBlog(path);
  }

}
