import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Blog } from '../_model/blog';
import { Observable, Subject } from 'rxjs';
import { IconParser } from '../_model/iconParser';
import * as iconParser from '../../icons.json';

@Injectable({
  providedIn: 'root'
})
export class BlogService {

  constructor(
    private http: HttpClient
  ) { }

  private baseUrl = 'https://api.github.com/repos/Chenxi-Zhang/Chenxi-Zhang.github.io/contents';
  private iconBaseUrl = 'https://raw.githubusercontent.com/Chenxi-Zhang/Chenxi-Zhang.github.io/master';
//   private token = 'a5cdf3332b933c89d578f34ca1d0947e6d62cd2d';
  private blogSubject = new Subject<Blog>();
  public blogToDisplay = this.blogSubject.asObservable();

  getBlogList(pathToBlog: string[]): Observable<Blog[]> {
    const url = this.baseUrl
      + (pathToBlog ? ('/' + pathToBlog.join('/')) : '');
    return this.http.get<Blog[]>(url);
  }

  getBlog(pathToBlog: string[]): void {
    const url = [this.baseUrl, ...pathToBlog].join('/');
    this.http.get<Blog>(url).subscribe(b => {
      this.blogSubject.next(b);
    });
  }

  buildPath(url: string): string[] {
    return url.split('/').slice(1);
  }

  isDir(blog: Blog): boolean {
    return blog.type === 'dir';
  }

  getBlogIconUrl(parser: IconParser, blog: Blog): string {
    return this._getIcon(parser, blog);
  }

  getOpenFolderIconUrl(parser: IconParser): string {
    return this._getOpenFolderIcon(parser);
  }

  getIconParser(): IconParser {
    for (const prop of Object.keys(iconParser)) {
      if (prop === 'default') {
        return iconParser[prop];
      }
    }
  }

  private _getIcon(parser: IconParser, blog: Blog): string {
    let def: string;
    if (this.isDir(blog)) {
      def = parser.folder;
    } else {
      const names = blog.name.split('.');
      let ext = '';
      if (names.length !== 1) {
        ext = names[names.length - 1];
      }
      def = parser.fileExtensions[ext] || parser.file;
    }
    return (parser.iconDefinitions[def]).iconPath;
  }

  private _getOpenFolderIcon(parser: IconParser): string {
    return parser.iconDefinitions[parser.folderExpanded].iconPath;
  }
}
